<template>
  <el-form
          :rules="rules"
          ref="loginForm"
          class="login-container"
          label-position="left"
          v-loading="loading"
          element-loading-text="正在登录"
          element-loading-spinner="el-icon-loading"
          element-loading-background="rgba(0, 0, 0, 0.8)"
          :model="loginForm">
    <h3 class="login-title">系统登陆</h3>
    <el-form-item  prop="account">
      <el-input type="text" v-model="loginForm.username" autocomplete="off" placeholder="账号"></el-input>
    </el-form-item>
    <el-form-item prop="pass">
      <el-input type="password" v-model="loginForm.password" autocomplete="off" placeholder="密码"></el-input>
    </el-form-item>
    <el-checkbox class="login_remember" v-model="checked" label-position="left">记住密码</el-checkbox>
    <el-form-item style="width: 100%;">
      <el-button type="primary" style="width: 100%" @click="submitClick('loginForm')">登录</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
export default {
  name: "Login",
  data() {
    return {
      rules: {
        account:[
            { required: true, message: '请输入用户名', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' }
        ]
      },
      checked: true,
      loginForm: {
        username: 'admin',
        password: 'abc123'
      },
      loading: false
    };
  },
  methods: {
    submitClick: function () {
      const _this = this;
      this.loading = true;
      this.postRequest('/login',{
        username: this.loginForm.username,
        password: this.loginForm.password
      }).then(resp => {
        _this.loading = false;
        if(resp && resp.status === 200){
          const data = resp.data();
          _this.$store.commit('login',data.obj);
          const path = _this.$store.query.redirect;
          _this.$router.replace({path: path === '/' || path === undefined ? '/home' : path});
        }
      });
    }
  }
}
</script>

<style scoped>
  .login-container {
    border-radius: 15px;
    background-clip: padding-box;
    margin: 180px auto;
    width: 300px;
    padding: 15px 35px 15px 35px;
    border: 1px solid #eaeaea;
    box-shadow: 0 0 25px #cac6c6;
  }
  .login-title {
    margin: 15px auto 20px auto;
    text-align: center;
    color: #505458;
  }
  .login_remember{
    margin: 0 0 15px 0;
    text-align: left;
  }
  .el-form-item__content{
    display: flex;
    align-items: center;
  }
</style>